import { NativeModules } from 'react-native';
import { checkPortraitSize } from './checkPortraitSize';

/**
 * 判断iOS的StatusBar高度是否是48
 * 包括11，xr
 */
export function isIOSStatusBar48() {
    // 暂时无法用宽高来判断 11 和 xr，因为 11 和 11 pro max 也是这个尺寸
    if (!checkPortraitSize(414, 896)) {
        return false;
    }
    const ret = NativeModules?.KRNBasic?.getDeviceInfoSync();
    if (ret) {
        const model = ret['data']['model'];
        if (model == 'iPhone12,1' || model == 'iPhone11,8') {
            return true;
        }
    }
    return false;
}
